import React, { Component } from "react";
import { ThemesItem } from "./template/ThemesItem";
import * as axios from "axios";

export class ContentPage extends Component {
  getThemes = () => {
    if (this.props.root.themes.length === 0) {
      axios.get(this.props.root.api.themes_api)
        .then((response) => {
          this.props.root.actionCreator("SET_THEMES", response.data[0]);
        });
    }
  }

  createSubitems = (items) => {
    return items.map((item, index) => (
      {
        link: item.link,
        value: item.value,
        key: index
      }
    ));
  }

  createThemesItem = (themes) => {
    this.getThemes();

    if (themes === undefined) { return [] }

    return themes.map((theme, index) => {
      return (<ThemesItem 
        { ...{
          link: {
            link: theme.title.link,
            value: theme.title.value,
            key: index
          },
          subitems: this.createSubitems(theme.items),
        }}
      />);
    })
  }

  render = () => {
    return (
      <React.StrictMode>
        <div className="main-container">
          <div className="main-item">
            <div className="tp-themes-block"> 
              <ul className="tp-themes-list">
                { this.createThemesItem(this.props.root.themes[0]) }
              </ul>
            </div>
          </div>
        </div>
      </React.StrictMode>
    );
  };
}
