import React, { Component } from "react";
import * as axios from "axios";

// Template components
import { LinksBlock } from "./template/LinksBlock";
import { Text } from "./template/Text";
import { ImageBlock } from "./template/ImageBlock";
import { CodeBlock } from "./template/CodeBlock";
import { Title } from "./template/Title";

export class HomePage extends Component {
  toggleMinimap = () => {
    if (this.props.root.minimap.mode === "minimap") {
      this.props.root.actionCreator("SWITCH_MINIMAP_MODE", // Turn on the minimap and check the button
        { minimapClassList: "minimap minimap_active", buttonClassList: "minimapButton_active" });

    } else{
      this.props.root.actionCreator("SWITCH_MINIMAP_MODE",  // Turn OFF the minimap and uncheck button
        { minimapClassList: "minimap", bottonClassList: "minimapButton" });
    }
  }

  getPageItems = () => {
    if (this.props.root.currentPage.length === 0) {
      axios.get(this.props.root.api.pages_api.page)
        .then((response) => {
          this.props.root.actionCreator("SET_CURRENT_PAGE", response.data[0]);
        });
    }
  }

  createPageItem = (item) => {
    switch(item.name) {
      case "title": {
        return <Title value={item.value} />
      }

      case "imageBlock": {
        return <ImageBlock
              image={item.items.image}
              id={item.items.id}
              sublinks={[ ...item.items.sublinks]}
            />
      }

      case "codeBox": {
        return <CodeBlock 
          lines={[ ...item.items.codeLines]} 
          result={[...item.items.resultLines]} 
          highlight={this.props.root.syntaxHighlight.highlight}
        />
      }

      case "text": {
        if (Object.keys(this.props.root.texts).length === 0) {
          axios.get(item.source)
            .then((response) => {
              this.props.root.actionCreator("ADD_NEW_TEXT", { name: item.id, text: response.data });
            });
        }
  
        return <Text value={this.props.root.texts[item.id]} id={item.id} />
      }

      case "links": {
        let links = item.items;
        return <LinksBlock links={[ ...links]} />
      }

      default: {
        return {};
      }
    }
  }

  createPage = (pageItems) => {
    let page = pageItems.map((item) => {
      return this.createPageItem(item);
    });

    return page;
  }

  createPagination = (pageId, nextPage) => {
    let previous, next;

    if (pageId < 2) {
      previous = <div></div>;
    } else {
      previous = <div className="tp-pagination_button previous-button" onClick={() => {this.setPage(pageId, -1)}}>
        <a href="#"><span>Previous</span></a>
      </div>
    }

    if (!nextPage) {
      next = <div></div>;
    } else {
      next = <div className="tp-pagination_button next-button" onClick={() => {this.setPage(pageId, 1)}}>
        <a href="#"><span>Next</span></a>
      </div>
    }

    return (
      <div className="tp-pagination_block">
        {previous}
        {next}
      </div>
    );
  }

  setPage = (pageId, inc) => {
    axios.get(`https://sha1om.github.io/dooboox_api/pages/page-${pageId + inc}`)
      .then((response) => {
        this.props.root.actionCreator("SET_CURRENT_PAGE", response.data[0]);
      });
  }

  render = () => {
    this.getPageItems();
    return (
      <React.StrictMode>
        <div id="minimapButton"
          className={this.props.root.minimap.buttonClassList}
          onClick={() => this.toggleMinimap()}>
            <span>MINIMAP</span>
        </div>
        <div className={"main-container"}>
          <div className="main-item">
            {this.createPage(this.props.root.currentPage)}
          </div>
          {this.createPagination(this.props.root.currentPageId, this.props.root.nextPage)}
        </div>
      </React.StrictMode>
    );
  }
}
